.TeaSlice {
  width: 65vw;
  height: 500px;
  object-fit: cover;
  @media screen and (min-width: 425px) {
    width: 150px;
  }
}
.teaTop {
  &.cake .TeaSlice {
    object-position: 11% 0;
  }
  &.mini .TeaSlice {
    object-position: 25% 0;
  }
  &.ball .TeaSlice {
    object-position: 35% 0;
  }
  &.square .TeaSlice {
    object-position: 32% 0;
  }
  &.brick .TeaSlice {
    object-position: 10% 0;
  }
}

// .wrapper .TeaSlice {
//   object-position: 40% 0;
// }
.soup {
  .TeaSlice {
    height: 75px;
    width: 100%;
    object-position: 0 55%;
    @media screen and (min-width: 1024px) {
      height: 500px;
      width: 150px;
      object-position: 40% 0;
    }
  }
  .TeaSliceInfo {
    margin-top: 50%;
    @media screen and (min-width: 1024px) {
      margin-top: 85%;
    }
  }
}

.TeaSliceInfoWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 120ms ease;
}
.TeaSliceInfo {
  background-color: rgba(0, 0, 0, 0.69);
  z-index: 10;
  margin-top: 85%;
  margin-left: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
  padding: 0.75rem;
  text-align: center;
  width: 95%;
  @media screen and (min-width: 1024px) {
    width: calc(150% - 0.5rem);
  }
}
.soup .TeaSliceInfo,
.wrapperTop .TeaSliceInfo {
  margin-left: 0.25rem;
  margin-right: 0.25rem;
}

.TeaSliceWrapper {
  position: relative;
  &.selected,
  &:hover {
    .TeaSliceInfoWrapper {
      opacity: 1;
      cursor: pointer;
    }
  }
}
